<template>
  <div class="basicInfo">
    <div>
      <div class="Btitle">
        <span class="basicData">账户总额</span>
      </div>
      <div class="happening">
        <!-- 三个金额,引用了数字滚动插件-->
        <div class="moneyHappening">
          <div :class="item.billClass" v-for="(item,i) in billOvj" :key="i">
            <div class="HappeningTxt">
              <router-link :class="item.linkClass" :to="{name:item.linkName}"></router-link>
              <span class="firstHappeningMoney">{{item.name}}</span>
              <span class="secondHappeningMoney">
                ￥
                <ICountUp
                  :delay="item.delay"
                  :endVal="item.money"
                  :options="options"
                  class="secondHappeningMoney"
                  style=" display: inline;"
                />
              </span>
              <span class="thirdHappeningMoney">{{item.child}}</span>
            </div>
          </div>
        </div>
        <div class="contact1">
          <div class="subTitle">账户充值</div>
          <ul>
            <li>
              <span class="listTitle">充值金额:</span>
              <span v-for="(item,index) in PurchaseTimeArr" :key="index">
                <span
                  :class="{saveBtn2:PurchaseTimeIndex==index,saveBtn1:PurchaseTimeIndex!=index[index],mainBtn:true}"
                  @click="purchaseMoneyClick(index,item)"
                >{{item}}元</span>
              </span>
              <span>
                <input
                  class="listInput margin-l"
                  @click="listInputClick"
                  type="number"
                  v-model="inputVal"
                  @keyup="blueFn(inputVal)"
                  placeholder="0"
                />
              </span>
              <span class="listInputTxt">元</span>
            </li>
            <li>
              <span class="listTitle">支付方式:</span>
              <span
                v-for="(item,i) in payObj"
                :key="i"
                :class="{saveBtn2:payType==(i+1),saveBtn1:payType!=(i+1)}"
                class="mainBtn"
                @click="payClick(i+1)"
              >
                <img :src="item.img" alt class="listTitleImg1" />
                <a>{{item.name}}</a>
              </span>
            </li>
            <li>
              <span class="listTitle">应付金额:</span>
              <span class="money">{{inputValBl?inputVal:AmountsPayable}}元</span>
            </li>
          </ul>
          <span class="saveBtn3 mainBtn" @click="WechatPay">
            <a>确认充值</a>
          </span>
        </div>
      </div>
      <div class="foter"></div>
    </div>
    <Wechat :WechatData="WechatData" v-if="WechatBl" :Wechatcode="Wechatcode" class="morePrompt"></Wechat>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import ICountUp from "vue-countup-v2";
import Wechat from "@/pages/UserCenter/UserItem/Wechat.vue";
export default {
  name: "accountOverview",
  components: {
    Wechat,
    ICountUp
  },
  data() {
    return {
      // 数字滚动插件
      delay: 500,
      options: {
        decimalPlaces: 2,
        separator: ",",
        decimal: ".",
        prefix: "",
        suffix: ""
      },
      // 微信支付组件
      Wechatcode: "",
      WechatBl: false,
      WechatData: {
        title: "密码",
        name: ["账号", "密码"],
        btn: [
          {
            text: "确认",
            callback(data) {}
          },
          {
            text: "取消",
            callback(data) {}
          }
        ]
      },
      // 充值金额
      PurchaseTimeArr: ["100", "500", "1000", "5000", "10000"],
      // 账单
      billOvj: [
        {
          linkName: "rechargeRecord",
          name: "账户余额",
          child: "充值记录",
          money: 0,
          billClass: "firstHappening",
          delay: 500,
          linkClass: "HappeningImg1"
        },
        {
          linkName: "consumingRecord",
          name: "消费金额",
          child: "消费记录",
          money: 0,
          billClass: "secondHappening",
          delay: 500,
          linkClass: "HappeningImg2"
        },
        {
          linkName: "incomeAmount",
          name: "收入金额",
          child: "收入明细",
          money: 0,
          billClass: "thirdHappening",
          delay: 500,
          linkClass: "HappeningImg3"
        }
      ],
      // 初始化应付金额
      AmountsPayable: 0,
      // 按钮颜色变化控制
      PurchaseTimeIndex: 999,
      // 支付样式控制
      payObj: [
        {
          name: "微信支付",
          img: require("@/assets/UserCenter/imgs/weixinzhifu.svg")
        },
        {
          name: "支付宝支付",
          img: require("@/assets/UserCenter/imgs/zhifubao.svg")
        }
      ],
      // 支付方式1为微信,2为支付宝
      payType: 1,
      // 输出金额框的默认值
      inputVal: "",
      // 展现给用户看的应付金额变化控制
      inputValBl: false
    };
  },
  methods: {
    // 初始化三种金额
    init() {
      this.$get("api/accountOverview").then(res => {
        if (res.code === "000000") {
          this.billOvj[2].money = parseFloat(res.data.Monetary);
          this.billOvj[1].money = parseFloat(res.data.credit);
          this.billOvj[0].money = parseFloat(res.data.accountBalance);
          this.$store.commit("userCenter/setBalance", res.data);
        }
      });
    },
    // 充值金额按钮点击事件
    purchaseMoneyClick(index, item) {
      // 当按钮被点击,清空输入框的输入值,变为0
      this.inputVal = 0;
      // 切换显示给用户看的应付金额,切换到按钮的值
      this.inputValBl = false;
      // 排他,处理按钮颜色
      this.PurchaseTimeIndex = index;
      // 把选中的金额赋值给应付金额
      this.AmountsPayable = item;
    },
    // 选中支付方式点击事件
    payClick(index) {
      // 微信为1--支付宝为2
      this.payType = parseInt(index);
    },
    // 输入框点击事件
    listInputClick() {
      // 点击后,应付金额为0--因为用户选中此处,要对应下方显示的应付金额为0 ,等用户点按钮再重新赋值
      this.AmountsPayable = 0;
      // 判断输入框内容是否为空
      if (this.inputVal != "") {
        // 不为空则把下方用户能看到的应付金额切换到与输入框绑定的值
        this.inputValBl = true;
      } else {
        // 为空,则继续显示按钮选中金额的值---0
        this.inputValBl = false;
      }
      // 把所有按钮样式改变
      this.PurchaseTimeIndex = 999;
    },
    // 输入框键盘事件
    blueFn(inputVal) {
      // 当键盘抬起时,把显示给用户看到的应付金额切换
      this.inputValBl = true;
      // 如果为空,应付金额切换到0,即this.AmountsPayable
      if (inputVal == "") {
        this.inputValBl = false;
      } else {
        this.inputValBl = true;
      }
    },
    // 获取微信支付二维码
    WechatCodeInit(money) {
      let vm = this;
      vm.$post("api/pay", {
        wayType: "wx",
        orderType: 1,
        name: "账户充值",
        money: money
      }).then(res => {
        vm.WechatBl = true;
        vm.Wechatcode = res.data;
        vm.WechatData.title = "微信支付";
        vm.WechatData.imgText = "请扫描下方二维码进行支付";
        vm.WechatData.name = ["1"];
        vm.WechatData.btn[0].callback = function(Arr, Wechatcode) {
          vm.WechatBl = false;
        };
      });
    },
    // 确认支付按钮
    WechatPay() {
      var vm = this;
      var money = this.inputValBl ? this.inputVal : this.AmountsPayable;
      if (
        money != "" &&
        money < 100000000 &&
        money > 0 &&
        vm.payType == 1 &&
        money.split(".").length < 3 &&
        money.split("e").length < 2
      ) {
        // 判断输入金额无误则获取二维码
        this.WechatCodeInit(money);
      } else if (vm.payType == 2) {
        this.$post("api/pay", {
          wayType: "ali",
          orderType: 1,
          name: "账户充值",
          money: money
        }).then(res => {
          const div = document.createElement("div");
          div.innerHTML = res;
          document.body.appendChild(div);
          document.forms[0].submit();
        });
      } else {
        this.tips.show(["请输入正确的充值金额", false]);
      }
    }
  },
  sockets: {
    message(msg) {
      this.WechatBl = false;
      this.tips.show([msg, true]);
      this.init();
    }
  },
  mounted() {
    this.init();

  }
};
</script>

